.card {
    background: #fff;
    border-radius: 10px;    
    position: relative;
    display: flex;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    padding: .1rem;
    transition: transform .2s;
    width: 250px;
    height: 260px;
    margin: 10px;

  }
.card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
    border-radius: 10px;
  }
.card > .img-container > img {
    width: 100%;   
    margin: -20px;
}
.card:hover {
    -ms-transform: scale(1.5); /* IE 9 */
    -webkit-transform: scale(1.5); /* Safari 3-8 */
    transform: scale(1.1); 
}
 
